ar(--text);\n    font-size: 16px;\n    border-radius: 24px;\n    border: 1px solid rgba(255, 255, 255, 0.182);\n    appearance: none;\n\n}\n\n\n\n.counter {\n    font-size: 12px;\n    border: 1px solid rgba(255, 255, 255, 0.164);\n    width: 25px;\n    height: 25px;\n    border-radius: 50%;\n\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: var(--secondary);\n}\n\n#main-tasks {\n    margin-top: 100px;\n}\n\nmain {\n    flex: 4;\n\n\n    display: flex;\n    flex-direction: column;\n    gap: 1rem;\n    padding: 1rem;\n\n\n}\n\n\n#to-do-container {\n    /* flex: 4;  */\n    border-radius: 24px;\n\n    padding: 2rem;\n    background-color: var(--primary);\n    display: flex;\n    height: 80%;\n    width: 80%;\n    margin-top: auto;\n    margin: auto;\n    margin-bottom: auto;\n\n}\n\ntable {\n    flex: 1;\n\n    display: flex;\n    flex-direction: column;\n\n    font-size: 24px;\n}\n\nthead {\n\n    display: block;\n    padding: 16px;\n    margin-right: 50px;\n}\n\ntr {\n    flex: 1;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    max-height: 60px;\n    min-height: 60px;\n    margin-right: 10px;\n\n\n}\n\ntd,\nth {\n    flex: 10;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\ntbody {\n    display: flex;\n    flex-direction: column;\n    gap: 16px;\n    height: 60%;\n\n    overflow-y: scroll;\n    overflow-x: hidden;\n    scroll-behavior: smooth;\n    /* --webkit-scrollbar-color: rgba(0, 0, 0, 0); */\n\n}\n\n::-webkit-scrollbar {\n    width: 10px;\n}\n\n\n::-webkit-scrollbar-track {\n    border-radius: 10px;\n    background: #444;\n    box-shadow: 0 0 1px 1px #111, inset 0 0 4px rgba(0, 0, 0, 0.3);\n}\n\n::-webkit-scrollbar-thumb {\n    border-radius: 10px;\n    background: linear-gradient(left, #3e3e3e, #111, #000);\n    box-shadow: inset 0 0 1px 1px #646464;\n}\n\ntbody>tr {\n    border: 1px solid rgba(0, 0, 0, 0);\n    background-color: var(--secondary);\n    padding: 16px;\n    border-radius: 24px;\n}\n\n\n.checkbox {\n    flex: 1;\n}\n\n\n#add-task {\n\n    margin-top: auto;\n    padding: 12px;\n    display: flex;\n    justify-content: flex-start;\n    align-items: flex-start;\n    flex: 1;\n    /* background-color: var(--primary); */\n    background-color: rgba(0, 0, 0, 0);\n    border: 1px solid var(--secondary);\n\n    position: fixed;\n    top: 73%;\n    width: 59%;\n    height: 10%;\n\n    /* z-index: 1; */\n\n\n}\n\n#add-task button {\n    width: 20px;\n    height: 30px;\n    padding: 18px;\n    border: 1px solid var(--secondary);\n\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    font-size: 32px;\n    border-radius: 24px;\n    background-color: var(--blue);\n}\n\n#add-task button:hover {\n    transform: scale(1.05);\n}\n\n#add-task button:active {\n    transform: scale(0.95);\n}\n\n#add-task td {\n    width: 100%;\n    height: 100%;\n}\n\n#button-td {\n    flex: 1;\n    width: 20px;\n}\n\n#add-task input {\n    flex: 1;\n    display: block;\n    /* width: 100%; */\n    height: 100%;\n    /* padding: 18px; */\n    /* border-radius: 24px; */\n    border: none;\n    background-color: rgba(0, 0, 0, 0);\n    font-size: 24px;\n    padding: 16px;\n    outline: none;\n}\n\n#error-placeholder {\n    position: fixed;\n    font-size: 18px;\n    color: white;\n    z-index: 2;\n    font-weight: 100;\n    top: 83%;\n    left: 32%;\n    color: red;\n    font-family: monospace;\n}\n\ntd.remove {\n    display: block;\n\n    flex: 0;\n    font-size: 22px;\n    color: rgba(255, 255, 255, 0.214);\n    outline: none;\n\n\n}\n\n\ntd.edit {\n    display: block;\n    flex: 0;\n    font: 22px;\n    color: rgba(255, 255, 255, 0.214);\n    margin-right: 10px;\n\n}\n\ntd.edit:hover {\n    color: white;\n}\n\n\n/* td.remove:active {\n    animation: tracking-in-expand 2s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;\n\n} */\n\ntd.remove:hover {\n    color: white;\n}\n\ninput[type=\"checkbox\"] {\n    