t-task-form>h3 {\n    color: rgb(162, 162, 162);\n    font-weight: 500;\n    font-family: Arial, Helvetica, sans-serif;\n    padding: 8px;\n    padding-left: 6px;\n    margin-top: 32px;\n\n\n}\n\n#date-editor {\n    width: 100%;\n    height: 24px;\n    font-size: 24px;\n    background-color: rgba(0, 0, 0, 0);\n    border: none;\n    outline: none;\n    padding: 8px;\n}\n\n#task-notes {\n    resize: none;\n    width: 100%;\n    height: 100px;\n    background-color: rgba(0, 0, 0, 0);\n    border: none;\n    border-radius: 8px;\n    padding: 8px;\n    /* padding-left: 12px; */\n    font-size: 18px;\n    outline: none;\n    font-family: Arial, Helvetica, sans-serif;\n}\n\n#task-notes:hover {\n    background-color: #000;\n}\n\n#task-notes:focus {\n    background-color: #000;\n}\n\n#delete-button {\n    margin-top: 64px;\n    color: #cc0018;\n    width: 100%;\n    height: 24px;\n    background-color: rgba(0, 0, 0, 0);\n    border: none;\n    font-size: 18px;\n    font-family: Arial, Helvetica, sans-serif;\n}\n\n#delete-button:hover {\n    color: #b40015;\n}\n\n#delete-button:active {\n    transform: translateY(1px);\n}\n\n\n@keyframes tracking-in-expand {\n    0% {\n        letter-spacing: -0.5em;\n        opacity: 0;\n    }\n\n    40% {\n        opacity: 0.6;\n    }\n\n    100% {\n        opacity: 1;\n    }\n}\n\n@keyframes scale-out-center {\n    0% {\n        transform: scale(1);\n        opacity: 1;\n    }\n\n    100% {\n        transform: scale(0);\n        opacity: 1;\n    }\n}\n\n@keyframes slide-out-right {\n    0% {\n        transform: translateX(0);\n        opacity: 1;\n    }\n\n    100% {\n        transform: translateX(1000px);\n        opacity: 0;\n    }\n}\n\n@keyframes rotate-out-hor {\n    0% {\n        transform: rotateX(360deg);\n        opacity: 1;\n    }\n\n    100% {\n        transform: rotateX(0deg);\n        opacity: 0;\n    }\n}\n\n@keyframes slide-out-left {\n    0% {\n        transform: translateX(0);\n        opacity: 1;\n    }\n\n    100% {\n  