montserrat);\n    background-color: var(--primary);\n    height: 100vh;\n    width: 100vw;\n\n    display: flex;\n    background-image: url('./bg.png');\n    background-repeat: no-repeat;\n    background-attachment: fixed;\n    background-position: center;\n\n}\n\n#sidebar {\n    flex: 1;\n    background-color: var(--primary);\n\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    gap: 16px;\n    font-family: \"Helvetica Neue\", Helvetica, Arial, Roboto, sans-serif;\n    font-weight: 200;\n\n\n}\n\n#sidebar>div {\n    width: 99%;\n    display: flex;\n    gap: 16px;\n    font-size: 24px;\n    align-items: center;\n    padding: 12px;\n}\n\n#sidebar>div:hover {\n    background-color: #25252b;\n}\n\n#date-filter {\n    display: flex;\n    /* gap: 16px */\n}\n\nh2 {\n    /* border: 1px solid red; */\n    width: 100%;\n    padding: 12px;\n    margin-top: 50px;\n    border-bottom: 1px solid rgba(255, 255, 255, 0.243);\n}\n\nh3 {\n    font-size: 18px;\n}\n\nselect {\n    width: 100%;\n    padding: 16px;\n    background-color: var(--primary);\n    color: var(--text);\n    font-size: 16px;\n    border-radius: 24px;\n    border: 1px solid rgba(255, 255, 255, 0.182);\n    appearance: none;\n\n}\n\n\n\n.counter {\n    font-size: 12px;\n    border: 1px solid rgba(255, 255, 255, 0.164);\n    width: 25px;\n    height: 25px;\n    border-radius: 50%;\n\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: var(--secondary);\n}\n\n#main-tasks {\n    margin-top: 100px;\n}\n\nmain {\n    flex: 4;\n\n\n    display: flex;\n    flex-direction: column;\n    gap: 1rem;\n    padding: 1rem;\n\n\n}\n\n\n#to-do-container {\n    /* flex: 4;  */\n    border-radius: 24px;\n\n    padding: 2rem;\n    background-color: var(--primary);\n    display: flex;\n    height: 80%;\n    width: 80%;\n    margin-top: auto;\n    margin: auto;\n    margin-bottom: auto;\n\n}\n\ntable {\n    flex: 1;\n\n    display: flex;\n    flex-direction: column;\n\n    font-size: 24px;\n}\n\nthead {\n\n    display: block;\n    padding: 16px;\n    margin-right: 50px;\n}\n\ntr {\n    flex: 1;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    max-height: 60px;\n    min-height: 60px;\n    margin-right: 10px;\n\n\n}\n\ntd,\nth {\n    flex: 10;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\ntbody {\n    display: flex;\n    flex-direction: column;\n    gap: 16px;\n    height: 60%;\n\n    overflow-y: scroll;\n    overflow-x: hidden;\n    scroll-behavior: smooth;\n    /* --webkit-scrollbar-color: rgba(0, 0, 0, 0); */\n\n}\n\n::-webkit-scrollbar {\n    width: 10px;\n}\n\n\n::-webkit-scrollbar-track {\n    border-radius: 10px;\n    background: #444;\n    box-shadow: 0 0 1px 1px #111, inset 0 0 4px rgba(0, 0, 0, 0.3);\n}\n\n::-webkit-scrollbar-thumb {\n    border-radius: 10px;\n    background: linear-gradient(left, #3e3e3e, #111, #000);\n    box-shadow: inset 0 0 1px 1px #646464;\n}\n\ntbody>tr {\n    border: 1px solid rgba(0, 0, 0, 0);\n    background-color: var(--secondary);\n    padding: 16px;\n    border-radius: 24px;\n}\n\n\n.checkbox {\n    flex: 1;\n}\n\n\n#add-task {\n\n    margin-top: auto;\n    padding: 12px;\n    display: flex;\n    justify-content: flex-start;\n    align-items: flex-start;\n    flex: 1;\n    /* background-color: var(--primary); */\n    background-color: rgba(0, 0, 0, 0);\n    border: 1px solid var(--secondary);\n\n    position: fixed;\n    top: 73%;\n    width: 59%;\n    height: 10%;\n\n    /* z-index: 1; */\n\n\n}\n\n#add-task button {\n    width: 20px;\n    height: 30px;\n    padding: 18px;\n    border: 1px solid var(--secondary);\n\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    font-size: 32px;\n    border-radius: 24px;\n    background-color: var(--blue);\n}\n\n#add-task button:hover {\n    transform: scale(1.05);\n}\n\n#add-task button:active {\n    transform: scale(0.95);\n}\n\n#add-task td {\n    width: 100%;\n    height: 100%;\n}\n\n#button-td {\n    flex: 1;\n    width: 20px;\n}\n\n#add-task input {\n    flex: 1;\n    display: block;\n    /* width: 100%; */\n    height: 100%;\n    /* padding: 18px; */\n    /* border-radius: 24px; */\n    border: none;\n    background-color: rgba(0, 0, 0, 0);\n    font-size: 24px;\n    padding: 16px;\n    outline: none;\n}\n\n#error-placeholder {\n    position: fixed;\n    font-size: 18px;\n    color: white;\n    z-index: 2;\n    font-weight: 100;\n    top: 83%;\n    left: 32%;\n    color: red;\n    font-family: monospace;\n}\n\ntd.remove {\n    display: block;\n\n    flex: 0;\n    font-size: 22px;\n    color: rgba(255, 255, 255, 0.214);\n    outline: none;\n\n\n}\n\n\ntd.edit {\n    display: block;\n    flex: 0;\n    font: 22px;\n    color: rgba(255, 255, 255, 0.214);\n    margin-right: 10px;\n\n}\n\ntd.edit:hover {\n    color: white;\n}\n\n\n/* td.remove:active {\n    animation: tracking-in-expand 2s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;\n\n} */\n\ntd.remove:hover {\n    color: white;\n}\n\ninput[type=\"checkbox\"] {\n    border-radius: 50%;\n    width: 1.3em;\n    height: 1.3em;\n    vertical-align: middle;\n    border: 1px solid #ddd;\n    appearance: none;\n    -webkit-appearance: none;\n    outline: none;\n    cursor: pointer;\n}\n\ninput[type=\"checkbox\"]:checked {\n    background-color: var(--doneGreen);\n}\n\n\n\n\n\ntbody>tr>td:nth-child(2) {\n    --width: 320px;\n    --max-width: 310px;\n    display: block;\n    text-align: center;\n    overflow: hidden;\n    white-space: nowrap;\n    text-overflow: ellipsis;\n    max-width: var(--max-width);\n    margin-left: calc(var(--width) - var(--max-width));\n\n}\n\n\n#form-container {\n    position: fixed;\n    width: 100vw;\n    height: 100vh;\n    z-index: 2;\n    /* backdrop-filter: blur(4px); */\n    backdrop-filter: brightness(50%);\n\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    display: none;\n\n\n}\n\n\n#edit-task-form {\n    background-color: var(--primary);\n    width: 500px;\n    height: 600px;\n    border-radius: 18px;\n\n    padding: 20px;\n    padding-left: 12px;\n\n    position: relative;\n    left: 10%;\n\n\n}\n\n#name-container {\n    width: 100%;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    padding: 2px;\n    gap: 16px;\n}\n\n#task-name {\n    width: 330px;\n    height: 24px;\n    border-radius: 8px;\n    background-color: rgba(0, 0, 0, 0);\n    border: none;\n    padding: 24px;\n    font-size: 24px;\n    outline: none;\n    font-family: var(--montserrat);\n    padding-left: 12px;\n\n\n}\n\n#done-button {\n    min-width: fit-content;\n    color: #0074e0;\n    font-family: Arial, Helvetica, sans-serif;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    /* height: 100%; */\n    font-size: 18px;\n}\n\n#done-button:hover {\n    color: #0064c2;\n}\n\n#done-button:active {\n    transform: translateY(1px);\n}\n\n#task-name:hover {\n    background-color: #000;\n}\n\n#task-name:focus {\n    background-color: #000;\n}\n\n#edit-task-form>h3 {\n    color: rgb(162, 162, 162);\n    font-weight: 500;\n    font-family: Arial, Helvetica, sans-serif;\n    padding: 8px;\n    padding-left: 6px;\n    margin-top: 32px;\n\n\n}\n\n#date-editor {\n    width: 100%;\n    height: 24px;\n    font-size: 24px;\n    background-color: rgba(0, 0, 0, 0);\n    border: none;\n    outline: none;\n    padding: 8px;\n}\n\n#task-notes {\n    resize: none;\n    width: 100%;\n    height: 100px;\n    background-color: rgba(0, 0, 0, 0);\n    border: none;\n    border-radius: 8px;\n    padding: 8px;\n    /* padding-left: 12px; */\n    font-size: 18px;\n    outline: none;\n    font-family: Arial, Helvetica, sans-serif;\n}\n\n#task-notes:hover {\n    background-color: #000;\n}\n\n#task-notes:focus {\n    background-color: #000;\n}\n\n#delete-button {\n    margin-top: 64px;\n    color: #cc0018;\n    width: 100%;\n    height: 24px;\n    background-color: rgba(0, 0, 0, 0);\n    border: none;\n    font-size: 18px;\n    font-family: Arial, Helvetica, sans-serif;\n}\n\n#delete-button:hover {\n    color: #b40015;\n}\n\n#delete-button:active {\n    transform: translateY(1px);\n}\n\n\n@keyframes tracking-in-expand {\n    0% {\n        letter-spacing: -0.5em;\n        opacity: 0;\n    }\n\n    40% {\n        opacity: 0.6;\n    }\n\n    100% {\n        opacity: 1;\n    }\n}\n\n@keyframes scale-out-center {\n    0% {\n        transform: scale(1);\n        opacity: 1;\n    }\n\n    100% {\n        transform: scale(0);\n        opacity: 1;\n    }\n}\n\n@keyframes slide-out-right {\n    0% {\n        transform: translateX(0);\n        opacity: 1;\n    }\n\n    100% {\n        transform: translateX(1000px);\n        opacity: 0;\n    }\n}\n\n@keyframes rotate-out-hor {\n    0% {\n        transform: rotateX(360deg);\n        opacity: 1;\n    }\n\n    100% {\n        transform: rotateX(0deg);\n        opacity: 0;\n    }\n}\n\n@keyframes slide-out-left {\n    0% {\n        transform: translateX(0);\n        opacity: 1;\n    }\n\n    100% {\n        transform: translateX(-1000px);\n        opacity: 0;\n    }\n}\n\n@keyframes fade-in-top {\n    0% {\n        transform: translateY(-50px);\n        opacity: 0;\n    }\n\n    100% {\n        transform: translateY(0);\n        opacity: 1;\n    }\n}\n@keyframes fade-out {\n    0% {\n      opacity: 1;\n    }\n    100% {\n      opacity: 0;\n    }\n  }\n  "],"sourceRoot":""}]);
